@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&family=Poppins:wght@200;300;400;500;600&display=swap);
body {
  
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{min-height:100vh;box-sizing:border-box;background-image:radial-gradient(farthest-corner at 10px 10px, #8bbabb, #cd6684, #ffe7ad, #ff677d);font-family:'Open Sans', sans-serif;padding:4.5rem}@media (max-width: 600px){.App{padding:1.7rem}}.main{display:flex;flex-wrap:wrap}.main .main-header{flex-grow:1;max-width:500px}.main .main-header h2{margin:0;font-size:3rem;font-weight:400;letter-spacing:.6rem}.main .main-header p{line-height:1.7rem}.main .content-section{flex-grow:3;max-width:800px}.main .content-section .accordeon-wrapper{max-width:100%}.main .content-section .accordeon-wrapper .accordeon{font-family:'Open Sans', sans-serif;font-size:inherit;border-style:none;background:none;cursor:pointer;border-bottom:solid black 1px;display:flex;align-items:center;justify-content:space-between;width:100%}.main .content-section .accordeon-wrapper .accordeon .accordeon-title{font-weight:300}.main .content-section .accordeon-wrapper .accordeon .rotate{transform:rotate(90deg)}.main .content-section .accordeon-wrapper .accordeon:hover,.main .content-section .accordeon-wrapper .active{color:#ebebeb;transition:.6s ease}.main .content-section .accordeon-wrapper .accordeon:hover .accordeon-icon path,.main .content-section .accordeon-wrapper .active .accordeon-icon path{fill:#ebebeb;transition:.6s ease}.main .content-section .accordeon-wrapper .accordeon-content{overflow:hidden;transition:max-height .6s ease}.main .content-section .accordeon-wrapper .accordeon-content .accordeon-text{font-weight:300}

